<template>
  	<div class="animated slideInLeft" style="animation-duration:100ms">
  		<div id="target" class="col-lg-15 control-section">
  			<div class="content-wrapper">
  				<ejs-toolbar id="toolbargrid2" :clicked="addEditHandler">
  					<e-items>
  						<e-item align="right" id="add" :template="addTemplate" :text="$ml.get('add')"></e-item>
  						<e-item align="right" id="delete" :template="deleteTemplate" :text="$ml.get('delete')"></e-item>
  					</e-items>
  				</ejs-toolbar>
  				<div class="control-section">
  					<ejs-grid id="studentgrid" ref='studentgrid' :showColumnChooser="true" :rowHeight='rowHeight' :allowResizing='true' :allowPdfExport="true" :height="height" :allowReordering="true" :allowExcelExport="true" :enableVirtualization="true" :dataSource="students" :allowFiltering='true' :filterSettings='filterOptions' :allowTextWraping="true" :allowSelection='true' :allowSorting='true' :actionBegin="actionBegin" :toolbarClick="clickHandler" :recordDoubleClick="editStudent" :toolbar="toolbar" :enableHover='false'>
  						<e-columns>
  							<e-column field='user_name' headerText='UserName'></e-column>
  							<e-column field='personal_details.name' headerText='Name'></e-column>
  							<e-column field='personal_details.email' headerText='Email'></e-column>
  							<e-column field='personal_details.phone' headerText='Phone'></e-column>
  							<e-column field='programme.name' headerText='Programme'></e-column>
  							<e-column field='academic_term.name' headerText='Academic-Term'></e-column>
  						</e-columns>
  					</ejs-grid>
  				</div>
  			</div>
  		</div>
  	</div>
</template>
	
<script>

	//Imports
	import { acadUrl } from '@/apiUrl'
	import axios from 'axios'
	import Vue from 'vue'
	import VueNotifications from 'vue-notifications'
	import miniToastr from 'mini-toastr'
	import { ToolbarPlugin } from "@syncfusion/ej2-vue-navigations";
	import {PdfExport, ExcelExport, Edit, ColumnMenu, Toolbar, Resize, ColumnChooser, Page, GridPlugin, VirtualScroll, Sort, Reorder, Filter, Selection, GridComponent, Group} from "@syncfusion/ej2-vue-grids";

	//Declarations
    var api = axios.create({
	  withCredentials :true
	})
	const toastTypes = {
	  success: 'success',
	  error: 'error',
	  info: 'info',
	  warn: 'warn'
	}
	const options = {
	  success: toast,
	  error: toast,
	  info: toast,
	  warn: toast
	}
	function toast ({title, message, type, timeout, cb}) {
	  return miniToastr[type](message, title, timeout, cb)
	}

	//Function Calls
	miniToastr.init({types: toastTypes})
	Vue.use(VueNotifications, options)
    Vue.use(GridPlugin);
	Vue.use(ToolbarPlugin)


    //Vue Handler
	export default {
		name:"StudentList",
		components:{

		},
		provide:{
			grid:[Group, PdfExport, ExcelExport, Edit, ColumnMenu, Resize, Filter, Selection, Sort, VirtualScroll, Toolbar, ColumnChooser, Reorder],
		},
		data : function () {
			return {
				//Grid Configurations
				rowHeight:30,
				height:window.innerHeight - 170,
				filterOptions: {
                    type: 'CheckBox'
                },
                toolbar: [
                	'Search', 'ColumnChooser', { prefixIcon: 'e-small-icon', id: 'big', align: 'Right' }, { prefixIcon: 'e-medium-icon', id: 'medium', align: 'Right' }, { prefixIcon: 'e-big-icon', id: 'small', align: 'Right' }
                ],
                students:[],
                //Toolbar Configurations
                addTemplate: function () {return {template: Vue.component("addTemplate", {template: `<b-badge id="label1" variant="success" ><i class="fa fa-plus"></i>&nbsp<span v-text="$ml.get('add')"></span></b-badge>`, data() {return {data: {}, }; }, }) } },
                deleteTemplate: function () {return {template: Vue.component("deleteTemplate", {template: `<b-badge id="label1" variant="primary" ><i class="fa fa-trash-o"></i>&nbsp<span v-text="$ml.get('delete')"></span></b-badge>`, data() {return {data: {}, }; }, }) } },
            }
		},
		methods:{
			// Grid Events
			actionBegin(args) {

			},
			editStudent(args) {
				this.$router.push(`/academics/student/details/${args.rowData._id}`)
			},
			// Toolbar Handler
			addEditHandler(args) {
             	 var selected = this.$refs.studentgrid.getSelectedRecords()
				// console.log(args)
				if(args.item.id=="add") {
					this.$router.push({name: "Add Student"})
				}
				if(args.item.id=="delete") {
                 	console.log(selected[0]._id);
                 	api.delete(`${acadUrl}stud/student/delete/one/${selected[0]._id}`).then((res)=> {
                 		console.log(res);
                 		api.get(`${acadUrl}stud/student/view/all`).then((students) => {
							this.students = students.data
						});
                 	});

				}
			},
			// Grid Toolbar Handler 
			clickHandler(args) {
				if (args.item.id === 'small') {
                    this.rowHeight = 20;
                }
                if (args.item.id === 'medium') {
                    this.rowHeight = 40;
                }
                if (args.item.id === 'big') {
                    this.rowHeight = 60;
                }
                if (args.item.text === 'Excel Export') {
                    this.$refs.overviewgrid.excelExport()
                }
                if (args.item.text === 'PDF Export') {
                    this.$refs.overviewgrid.pdfExport()
                }
			},
		},
		mounted(){
			//Get Student List
			api.get(`${acadUrl}stud/student/view/all`).then((students) => {
				this.students = students.data
			});
		},
	};
</script>

<style scoped>
	#studentgrid {
		min-height: calc( 100vh - 93px );
	}
</style>